@import '../../sass/main.scss';

.auth {
  @include flex(column, flex-start, center);
  width: 100%;
}

.auth__content {
  @include flex(row, space-around);
  width: 100%;
}

.auth__line {
  @include border(bottom);
  @include margin(20px, top);
  background-color: $gray;
  height: auto;
  width: 2px;
}

.auth__loading {
  @include loading-full;
}

.auth__message-error {
  background-color: $lightorange;
  color: $black;
  font-weight: bold;
  padding: 0 8px;
  text-align: center;
  width: 100%;
}

.auth__message-successful {
  background-color: $lightgreen;
  color: $black;
  font-weight: bold;
  padding: 0 8px;
  text-align: center;
  width: 100%;
}
