@import '../../sass/main.scss';

.stuffie {
  @include padding(4px, 'top');
  width: 1024px;
}

.stuffie__header {
  border-bottom: 2px solid $blue; 
  width: 100%;
}

.stuffie__main {
  @include flex(row, space-between, left);
  margin-top: 8px;
  width: 100%;
}

.stuffie__menu {
  @include section-background;
  min-height: 250px;
  max-height: 250px;
  width: 200px;
}

.stuffie__content {
  @include content-background;
  @include margin(8px, 'horizontal');
  width: 65%;
}

.stuffie__right {
  @include flex(column);
}

.stuffie__user {
  @include flex(row, flex-start, center);
  @include section-background;
  @include margin(8px, bottom);
  width: 200px;
}

.stuffie__welcome {
  @include margin(4px, left);
  font-weight: bold;
  font-size: 16px;
}

.stuffie__apps {
  @include section-background;
  width: 200px;
}
